export function getBubbleSortAnimation(array) {
    const animations = [];
    if(array.length <= 1) return array;

    const auxiliaryArray = array.slice();
    bubbleSort(array, auxiliaryArray, animations);
    return animations;
}

function bubbleSort(mainArray, auxiliaryArray, animations) {
    
    for(let i = 0; i < auxiliaryArray.length-1; i++) {
        for(let j = 0; j < auxiliaryArray.length - i-1; j++) {
            animations.push([j, j+1, -1, -1]); // for red
            animations.push([j, j+1, -1, -1]); // for blue
            if(auxiliaryArray[j] > auxiliaryArray[j+1]) {
                animations.push([j, auxiliaryArray[j+1], j+1,auxiliaryArray[j]]); // for overwriting
                // animations.push([j+1, auxiliaryArray[j]]);
                const temp = auxiliaryArray[j];
                auxiliaryArray[j] = auxiliaryArray[j+1];
                auxiliaryArray[j+1] = temp;
            } else {
                animations.push([j, auxiliaryArray[j], j+1, auxiliaryArray[j+1]]);
                // animations.push([j+1, auxiliaryArray[j+1]]);
            }
        }
    }
}