export function getInsertionSortAnimation(array) {
    const animations = [];
    if(array.length <= 1) return array;

    const auxiliaryArray = array.slice();
    insertionSort(array, auxiliaryArray, animations);
    return animations;
}

function insertionSort(mainArray, auxiliaryArray, animations) {
    
    for(let i = 1; i < auxiliaryArray.length; i++) {
        let temp = auxiliaryArray[i];
        let j = i-1;
        while(j>=0 && auxiliaryArray[j] > temp) {
            animations.push([j+1, auxiliaryArray[j], "red"]);  
            animations.push([j+1, auxiliaryArray[j], "green"]);  
            auxiliaryArray[j+1] = auxiliaryArray[j];
            j--;
        }
        animations.push([j+1, temp, "green"]);
        auxiliaryArray[j+1] = temp;

    }
}