import React from "react";
import './SortingVisualizer.css';
import { getMergeSortAnimation } from "../SortingAlgorithms/SortingAlgorithms";
import { getBubbleSortAnimation } from "../SortingAlgorithms/BubbleSort";
import { getSelectionSortAnimation } from "../SortingAlgorithms/SelectionSort";
import { getInsertionSortAnimation } from "../SortingAlgorithms/InsertionSort";
import {DebounceInput} from 'react-debounce-input';

const ANIMATION_SPEED_MS = 10;
// const NUMBER_OF_ARRAY_BARS = 140;
const PRIMARY_COLOR = 'blue';
const SECONDARY_COLOR = 'red';
const GREEN = 'green';

export default class SortingVisualizer extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
            array: [],
            numberOfArrayBars: 25
        };
    }

    componentDidMount() {
        this.resetArray();
    }
    changeHandler = (e) => {
        this.setState({numberOfArrayBars:e.target.value});
        this.resetArray();
    }
    resetArray() {
        const array = [];
        const numberOfArrayBars = this.state.numberOfArrayBars;
        for(let i = 0; i < numberOfArrayBars; i++){
            array.push(randomIntFromIntervals(5,350));
        }
        let bars = document.getElementsByClassName('array-bar');
        for(let i = 0; i < array.length; i++) {
            setTimeout(()=>{
                bars[i].style.backgroundColor = PRIMARY_COLOR;
            }, i*1);
        }
        
        this.setState({array});
    }

    mergeSort() {
        const animations = getMergeSortAnimation(this.state.array);
        for(let i = 0; i < animations.length; i++) {
            const arrayBars = document.getElementsByClassName('array-bar');
            const isColorChange = i % 3 !== 2;
            if(isColorChange) {
                const [barOneIdx, barTwoIdx] = animations[i];
                const barOneStyle = arrayBars[barOneIdx].style;
                const barTwoStyle = arrayBars[barTwoIdx].style;
                const color = i%3 === 0 ? SECONDARY_COLOR : PRIMARY_COLOR;
                setTimeout(() => {
                    barOneStyle.backgroundColor = color;
                    barTwoStyle.backgroundColor = color;
                }, i * ANIMATION_SPEED_MS);
            } else {
                setTimeout(() => {
                    const [barOneIdx, newHeight] = animations[i];
                    const barOneStyle = arrayBars[barOneIdx].style;
                    barOneStyle.height = `${newHeight}px`;
                }, i * ANIMATION_SPEED_MS);
            }
        }
    }
    
    bubbleSort() {
        const animations = getBubbleSortAnimation(this.state.array);
        const numberOfArrayBars = this.state.numberOfArrayBars;
        for(let i = 0; i < animations.length; i++) {
            const arrayBars = document.getElementsByClassName('array-bar');
            const isColorChange = i % 3 !== 2;
            if(isColorChange) {
                const barOneIdx= animations[i][0];
                const barTwoIdx= animations[i][1];
                const barOneStyle = arrayBars[barOneIdx].style;
                const barTwoStyle = barTwoIdx < numberOfArrayBars? arrayBars[barTwoIdx].style :{};
                const color = i%3 === 0 ? SECONDARY_COLOR : PRIMARY_COLOR;
                setTimeout(() => {
                    barOneStyle.backgroundColor = color;
                    barTwoStyle.backgroundColor = color;
                }, i * ANIMATION_SPEED_MS);
            } else {
                setTimeout(() => {
                    const [barOneIdx, newHeight1, barTwoIdx, newHeight2] = animations[i];
                    const barOneStyle = arrayBars[barOneIdx].style;
                    const barTwoStyle = arrayBars[barTwoIdx].style;
                    barOneStyle.height = `${newHeight1}px`;
                    barTwoStyle.height = `${newHeight2}px`;
                    barTwoStyle.backgroundColor = GREEN;
                }, i * ANIMATION_SPEED_MS);
            }
        }
    }

    selectionSort() {
        const animations = getSelectionSortAnimation(this.state.array);
        for(let i = 0; i < animations.length; i++) {
            const arrayBars = document.getElementsByClassName('array-bar');
            const isColorChange = animations[i][2] < 0;
            if(isColorChange) {
                const barOneIdx = animations[i][0]; // left
                const barTwoIdx = animations[i][1]; // right
                const barOneStyle = arrayBars[barOneIdx].style;
                const barTwoStyle = arrayBars[barTwoIdx].style;
                
                setTimeout(() => {
                    if(animations[i][2] === -2) {
                        barOneStyle.backgroundColor = SECONDARY_COLOR;
                        barTwoStyle.backgroundColor = PRIMARY_COLOR;
                    } else if(animations[i][2] === -1){
                        barOneStyle.backgroundColor = SECONDARY_COLOR;
                        barTwoStyle.backgroundColor = SECONDARY_COLOR;
                    } else if(animations[i][2] === -3){
                        barOneStyle.backgroundColor = GREEN;
                        barTwoStyle.backgroundColor = GREEN;
                    } 
                    
                }, i * ANIMATION_SPEED_MS);
            } else {
                setTimeout(() => {
                    const [barOneIdx, newHeight1, barTwoIdx, newHeight2] = animations[i];
                    const barOneStyle = arrayBars[barOneIdx].style;
                    const barTwoStyle = arrayBars[barTwoIdx].style;
                    barOneStyle.height = `${newHeight1}px`;
                    barTwoStyle.height = `${newHeight2}px`;
                    // barOneStyle.backgroundColor = GREEN;
                    // barTwoStyle.backgroundColor = GREEN;
                    
                }, i * ANIMATION_SPEED_MS);
            }
        }
    }

    insertionSort() {
        const animations = getInsertionSortAnimation(this.state.array);
        for(let i = 0; i < animations.length; i++) {
            const arrayBars = document.getElementsByClassName('array-bar');
            setTimeout(() => {
                const barOneIdx = animations[i][0];
                const newHeight = animations[i][1];
                const color = animations[i][2];
                const barOneStyle = arrayBars[barOneIdx].style;
                barOneStyle.height = `${newHeight}px`;
                barOneStyle.backgroundColor = color;
            }, i * ANIMATION_SPEED_MS);
        }   
    }

    render() {
        const {array} = this.state;
        // const {numberOfArrayBars} = this.state;
        return(
            <div className="array-container">
                <h1>SORTING VISUALIZER</h1>
             {array.map((value, idx) => (
                <div 
                    className="array-bar" 
                    key={idx}
                    style={{height: `${value}px`}}>
                </div> 
             ))}<br></br>
             <button className="btn" onClick={()=> this.resetArray()}>Generate Array</button>
             <button className="btn" onClick={()=> this.mergeSort()}>Merge Sort</button>
             <button className="btn" onClick={() => this.bubbleSort()}>Bubble Sort</button>
             <button className="btn" onClick={() => this.selectionSort()}>Selection Sort</button>
             <button className="btn" onClick={() => this.insertionSort()}>Insertion Sort</button>
             <div className="slidecontainer">
                 <h5>Slide to change size of arrays : </h5>
                 <DebounceInput minLength={2} debounceTimeout={30} type="range" min="10" max="130" value={this.state.numberOfArrayBars} id="myRange" onChange={this.changeHandler}></DebounceInput>
             </div>
             <br></br>
             
            </div>
        );
    }
}

function randomIntFromIntervals(min, max){
    return Math.floor(Math.random() * (max - min + 1) + min)
}


