export function getMergeSortAnimation(array) {
    const animations = [];
    if (array.length <= 1) return array;

    const auxiliaryArray = array.slice();
    mergeSortHelper(array, 0, array.length-1, auxiliaryArray, animations);
    return animations;
}

function mergeSortHelper(
    mainArray,
    start,  
    end,
    auxiliaryArray,
    animations
) {
    if(start === end) return;
    const midIdx = Math.floor((start + end) / 2);
    mergeSortHelper(auxiliaryArray, start, midIdx, mainArray, animations);
    mergeSortHelper(auxiliaryArray, midIdx+1, end, mainArray, animations);
    doMerge(mainArray, start, midIdx, end, auxiliaryArray, animations);
}

function doMerge(
    mainArray,
    start,
    mid,
    end,
    auxiliaryArray,
    animations
) {
    let i = start, j = mid+1, k = start;
    while(i <= mid && j <= end){
        // first push to change to red color
        animations.push([i, j]);

        // second push to change to blue color
        animations.push([i, j]);

        if(auxiliaryArray[i] <= auxiliaryArray[j]){
            // overwrite the value at index k in the original array with value at 
            // index i in auxiliaryArray
            animations.push([k, auxiliaryArray[i]]);
            mainArray[k++] = auxiliaryArray[i++];
        } else{
            // overwrite the value at index k in original array with value at 
            // index j in auxiliary array
            animations.push([k, auxiliaryArray[j]]);
            mainArray[k++] = auxiliaryArray[j++];
        }        
    }
    while(i <= mid) {
        animations.push([i, i]);
        animations.push([i, i]);
        animations.push([k, auxiliaryArray[i]]);
        mainArray[k++] = auxiliaryArray[i++];
    }

    while(j <= end) {
        animations.push([j, j]);
        animations.push([j, j]);
        animations.push([k, auxiliaryArray[j]]);
        mainArray[k++] = auxiliaryArray[j++];
    }
}