export function getSelectionSortAnimation(array) {
    const animations = [];
    if(array.length <= 1) return array;
    const auxiliaryArray = array.slice();
    selectionSort(array, auxiliaryArray, animations);
    return animations;
}

function selectionSort(mainArray, auxiliaryArray, animations) {

    for(let i = 0; i < auxiliaryArray.length - 1; i++) {
        let min = i;
        for(let j = i+1; j < auxiliaryArray.length; j++) {
            animations.push([min, j, -1, -1]); // for red bar
            if(auxiliaryArray[j] < auxiliaryArray[min]){
                animations.push([j, min, -2,-2]); // for blue
                min = j;
            } else {
                animations.push([min, j, -2, -2]); // for blue
            }
        }
        if(min !== i) { // Swap
            animations.push([i, auxiliaryArray[min], min, auxiliaryArray[i]]);
            let temp = auxiliaryArray[min];
            auxiliaryArray[min] = auxiliaryArray[i];
            auxiliaryArray[i] = temp;
        } else {
            animations.push([i, auxiliaryArray[i], min, auxiliaryArray[min]]);
        }
        animations.push([i, i, -3, -3]);
    }
}